	module.exports = {"title":"Accessibility Quick Wins","date":"2015-05-08T22:12:03.284Z","layout":"post","path":"/accessibility-quick-wins/","category":"Accessibility","description":"Improve Accessibility With These Quick Wins","body":"<p>Accessibility does’t have to be hard, you shouldn’t need to read reems of documentation or worry about the extra time it will take.</p>\n<p>I started to take an increasing interest in accessibility when I realised I was having to jump through hoops in order to use the internet. I primarily use a keyboard and have shocking eyesight so I tend to change the text size a lot. Most websites don’t work correctly and left me with creating my own stylesheets!</p>\n<p>I am guilty of not thinking about accessibility in the past but I have taken steps to change this. In all my reading, I have found the below to be quick wins that will aid accessibility and usability for all:</p>\n<ul>\n<li>Browsers are really good at making websites accessible via a keyboard without you having to do much. Using the correct element is quite important. Many times I have seen an <code>&lt;a&gt;</code> and <code>&lt;button&gt;</code> used incorrectly or sometimes a <code>&lt;div&gt;</code> will be used instead. Buttons should be used for form controls that perform actions and links should be used to navigate. Unplug your mouse and try to use your website using only the keyboard. This will highlight areas for improvements.</li>\n<li>Ensure you are using ARIA attributes.</li>\n<li>Test your content with a screen reader. <a href=\"https://chrome.google.com/webstore/detail/chromevox/kgejglhpjiefppelpmljglcjbhoiplfn?hl=en\">Chrome Vox</a> is a free Google Chrome Extension. Using a screen reader will highlight repeated content or sections that don’t make sense.</li>\n<li>Mark up forms correctly. Most screen readers have modes specifically for form interaction so be as semantic as necessary and ensure you are associating Labels With Inputs.</li>\n<li>One accessibility concern that I personally have difficulty with is contrast. Not everyone has access to top of the range monitors, nor does everyone have perfect eyesight. An excellent tool to check contrast is the <a href=\"http://leaverou.github.io/contrast-ratio/#gray-on-lightgray\">contrast ratio</a> by <a href=\"http://lea.verou.me/\">Lea Verou</a>. The <a href=\"http://contrastrebellion.com/\">Contrast Rebellion</a> does an excellent job at explaining why contrast is important on the web.</li>\n<li>Text sizing. Ensure that users can change the text size and/or zoom without breaking your layout.</li>\n</ul>\n<p>Nearly all of these quick wins are simple and can be achieved quite easily. They will benefit accessibility but also usability for people without accessibility concerns. Applying common sense and caring about your users should be the quickest win.</p>\n"}

