	module.exports = {"title":"Future of Navigation","date":"2016-11-19T22:12:03.284Z","layout":"post","path":"/future-of-navigation/","category":"User Experience","description":"What Is The Future of Navigation","body":"<p>Navigation on the web sucks. Large websites can be hard to use and oftentimes send you to the wrong place. Most e-commerce websites I use are terrible at navigation.</p>\n<p>As it currently stands maintaining separate location tools, in this case a main navigation and search on websites usually means one gets neglected as we think we have covered how to get to the content.</p>\n<p>I have always been more search-dominant so if a website has a good search tool, i’ll use that over a mega menu.</p>\n<p><a href=\"http://www.amazon.co.uk/\">Amazon</a> get’s it right in my opinion. You select the category you want, and search for what you want.</p>\n<p>Another great example of search would be <a href=\"http://vans.co.uk/\">Vans</a>. Their search is simple to use and easy to find. Their categorisation of products is also easy to follow so I have more options.</p>\n<p>Search over navigation makes much more sense to me but I’m aware this isn’t a catchall solution as people will habitually search for books, games, DVDs and CDs so products work (like Amazon and Vans) but possibly not pages, at least not yet.</p>\n<p>I would love for a large product based website opt for search over navigation and it seems I am not alone in this thinking:</p>\n<blockquote class=\"twitter-tweet\" lang=\"en\"><p lang=\"en\" dir=\"ltr\">I&#39;d be interested to see a large ecom site with hundreds of categories drop their main nav in favour of sophisticated search.</p>&mdash; James Seymour-Lock (@JamesSLock) <a href=\"https://twitter.com/JamesSLock/status/621544369229709313\">July 16, 2015</a></blockquote>\n<script async src=\"//platform.twitter.com/widgets.js\" charset=\"utf-8\"></script>\n<p>I don’t know the future of navigation on the web but I think search will play a large part.</p>\n"}

