	module.exports = {"title":"Lean UX","date":"2017-02-08T22:12:03.284Z","layout":"post","path":"/lean-ux/","category":"User Experience","description":"Lean UX Development Theories","body":"<p>The newest buzz word on the block is Lean UX. Lean UX is basically doing UX in the context of Agile development. It’s a faster more iterative approach to web design.</p>\n<p>I’ve often said that designing or building a website is never over but now I have realised that the best websites keep evolving and changing and those that don’t tend to stagnate and get redesigned every 5 or so years only to stagnate again.</p>\n<p>I’m looking at this from a agency to client point of view. I’d love to see a massive shake up in how agency’s deal with small to medium clients.</p>\n<p>The main reason why a website or web app needs to evolve is because it needs to evolve with its users. People change, they have habits, change habits and develop new habits. Just because your checkout process works today, doesn’t mean it will in a years time. You need to keep analysing the websites data and keep user testing to see if design ideas are correct and to keep up with the pace of todays users.</p>\n<p>The best example of this would be <a href=\"http://mailchimp.com/\">Mail Chimp</a>. They are constantly tweaking their design and take on a more data led design approach. The result is a much better experience for the user and more money for mail chimp.</p>\n<p>By keeping a check on your decisions, you can much easily update sections that are not working and ensure you or your client keeps making money. Instead of redesigning every 5 years at a huge cost you can take a much more modular approach and the end result is a much better website that is both being used and making users happy.</p>\n<p>Of course cost is a factor and this process is much too alien for some clients to ever agree to but it’s up to us as the experts to steer them in the right direction. Sometimes clients don’t see their website the way we do and that’s ok, this process isn’t for them, it’s for the end users.</p>\n<p>An excellent book on this topic is <a href=\"http://www.amazon.com/dp/1449311652/\">Lean UX</a>. Buy it, it just may help you to get your company onboard.</p>\n"}

