	module.exports = {"title":"When to use buttons","date":"2017-03-02T22:12:03.284Z","layout":"post","path":"/when-to-use-buttons/","category":"Front End","description":"When To Use Buttons In HTML","body":"<p>When to use a button is often confusing for beginners and advanced HTML authors alike. The confusion is made worse by the many different types of buttons that you can use and personal preferences.</p>\n<h2>Buttons :</h2>\n<p><code>&lt;button&gt;Button&lt;/button&gt;</code></p>\n<p><code>&lt;button type=&quot;submit&quot;&gt;Submit button&lt;/button&gt;</code></p>\n<h2>Input Buttons :</h2>\n<p><code>&lt;input type=&quot;button&quot; value=&quot;Input button&quot;&gt;</code></p>\n<p><code>&lt;input type=&quot;submit&quot; value=&quot;Input submit button&quot;&gt;</code></p>\n<h2>When do you use a button?</h2>\n<p>You can use a <code>&lt;button&gt;</code> inside a <code>&lt;form&gt;</code>, as it will behave exactly like a <code>&lt;button type=&quot;submit&quot;&gt;Submit button&lt;/button&gt;</code>. The button element was introduced to HTML as an alternative to inputs as they are easier to style. By default, a buttons type is set to submit like so <code>&lt;button type=&quot;submit&quot;&gt;Submit button&lt;/button&gt;</code>.</p>\n<p>When you have an action on the page and you need an element to trigger that action, rather than using an empty link, use a <code>&lt;button&gt;</code>. If you need to link somewhere, say to another page or a website use <code>&lt;a href=&quot;#&quot;&gt;&lt;/a&gt;</code> but for everything else, both <code>&lt;button&gt;</code> and <code>&lt;input&gt;</code> are perfectly valid so which you choose is down to personal preference and is less about semantics at this point.</p>\n<h2>Styling?</h2>\n<p>There are styling differences between buttons and inputs but I suggest you read this <a href=\"http://nickcowie.com/presentation/s5-button.html\">presentation from Nick Cowie</a> for more information on that.</p>\n"}

