	module.exports = {"title":"ARIA labelledby","date":"2017-06-02T22:12:03.284Z","layout":"post","path":"/aria-labelledby/","category":"Accessibility","description":"ARIA labelledby Explained","body":"<p>The <code>aria-labelledby</code> attribute can be used to provide an accessible name to a subset of elements. It’s used to associate a group of elements, or to establish a relationship between groups. It’s function works similar to the alt and title attributes.</p>\n<p>Semantic markup is always preferred over ARIA misuse. With that said, it isn’t always possible to access the markup in certain situations so <code>aria-labelledby</code> is essential for letting a screenreader know what your intent is.</p>\n<p>The example below uses the <code>aria-labelledby</code> property to indicate where the section’s title is. The value must match the id of the element that contains the <code>&lt;section&gt;</code> title</p>\n<pre><code>&lt;<span class=\"hljs-selector-tag\">section</span> aria-labelledby=<span class=\"hljs-string\">\"section-heading\"</span>&gt;\n    &lt;<span class=\"hljs-selector-tag\">h2</span> id=<span class=\"hljs-string\">\"section-heading\"</span>&gt;I am <span class=\"hljs-selector-tag\">a</span> title&lt;/h2&gt;\n    &lt;<span class=\"hljs-selector-tag\">article</span> role=<span class=\"hljs-string\">\"article\"</span>&gt;\n        I am <span class=\"hljs-selector-tag\">a</span> some <span class=\"hljs-selector-tag\">article</span> text\n    &lt;/article&gt;     \n&lt;/section&gt;\n</code></pre>\n<p>Another example would be to associate titles with lists. Often lists will be preceded by a title but the two are unrelated. <strong>Labelledby</strong> provides more contextual information to users when they are navigating the page.</p>\n<pre><code><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">h3</span> <span class=\"hljs-attr\">id</span>=<span class=\"hljs-string\">\"colours\"</span>&gt;</span>Colours of the rainbow<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">h3</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">ul</span> <span class=\"hljs-attr\">aria-labelledby</span>=<span class=\"hljs-string\">\"colours\"</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">li</span>&gt;</span>Red<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">li</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">li</span>&gt;</span>Yellow<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">li</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">li</span>&gt;</span>Pink<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">li</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">li</span>&gt;</span>Green<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">li</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">ul</span>&gt;</span>\n</code></pre>\n<h2>Example scenarios</h2>\n<p>These are only a few examples of what you would use <code>aria-labelledby</code> for in the wild.</p>\n<ul>\n<li>When used in a form context, the <code>aria-labelledby</code> attribute replaces the associated label element. This can be useful when you require multiple labels.</li>\n<li>Radio Groups</li>\n<li>Associating Headings With Regions</li>\n<li>Giving meaning to menus</li>\n</ul>\n"}

